import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { styled } from '@mui/material/styles'
import { BTitle } from 'components/CommonComponent'
import Card from 'components/Card'
import Table, { StyledTableCell } from 'components/Table'
import useEpoch, { useEpochInfo } from 'hooks/useEpoch'
import Select from 'components/Select'
import { usePositionAction, usePositionInfo } from 'hooks/news/usePoistion'
import { mintTokenChainConfigs, MintTokenKey } from 'config/constants/mintToken'
import { useWeb3React } from 'web3-react-core'
import { isSupportedChainId } from 'utils/web3React'
import { SupportedChainId } from 'config/constants/chains'
import Logo from 'components/Logo'
import { Epoch } from 'state/epochSlice'
import { localeData } from 'moment'

const Wrapper = styled('div')`
  padding-right: 40px;
  height: 80vh;
  overflow: scroll;
`

const TableCard = styled(Card)`
  margin-bottom: 20px;
`

const columns = [
  {
    id: 'epoch',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Epoch',
  },
  {
    id: 'start',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Start',
  },
  {
    id: 'factor',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Net Rewards',
  },
  /*  {
    id: 'acc',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Acc ',
  }, */
]

function DashboardPage() {
  // const { loadData, epochMap, pagination, currentPage, loading, onClear } = useEpoch()

  const [data, setData] = useState<Array<Epoch>>([])

  const { loadData, epochCurent } = useEpochInfo()

  
  const { currentMintToken } = usePositionInfo()
  const { handleChangeCurrentPosition } = usePositionAction()
  const { chainId } = useWeb3React()
  const pageTotal = useMemo(()=>{
    return Number(epochCurent)
  }, [epochCurent])
  const mintTokens = useMemo(() => {
    if (isSupportedChainId(chainId)) {
      const _tokens = mintTokenChainConfigs[chainId as SupportedChainId].tokens
      return Object.keys(mintTokenChainConfigs[chainId as SupportedChainId].tokens).map((key) => {
        return {
          token: _tokens[key as MintTokenKey].symbol,
          address: _tokens[key as MintTokenKey].address,
          decimals: _tokens[key as MintTokenKey].decimals,
          icon: () => (
            <Logo
              imgprops={{ style: { marginRight: 10 } }}
              token={_tokens[key as MintTokenKey].logo}
              symbol={_tokens[key as MintTokenKey].symbol}
            />
          ),
        }
      })
    }
    return []
  }, [chainId])

  const handleGetEpoch = useCallback(async (pageNo = 1)=>{
    setData([])
    const list = await loadData(pageNo);
    setData(list)
  }, [loadData])


  useEffect(()=>{
    handleGetEpoch(1)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentMintToken, epochCurent])




  return (
    <>
      <BTitle>Epoch Stat</BTitle>
      <Select
        selectValue={currentMintToken}
        options={mintTokens}
        isGroup={false}
        onChange={(e) => {
          handleChangeCurrentPosition(e)
          setData([])
        }}
      />
      <Wrapper>
        <TableCard key={currentMintToken}>
          <Table
            dataSource={data}
            columns={columns}
            pagination
            content={(row, index, labelId) => (
              <>
                <StyledTableCell component="th" id={labelId} scope="row">
                  {row.epoch}
                </StyledTableCell>
                <StyledTableCell>{row.start}</StyledTableCell>
                <StyledTableCell>{row.factor} </StyledTableCell>
                {/*   <StyledTableCell>{row.acc}</StyledTableCell>
                 */}{' '}
              </>
            )}
            onChangePage={(_page) => {
              handleGetEpoch(_page + 1);
            }}
            pages={pageTotal}
          />
        </TableCard>
      </Wrapper>
    </>
  )
}

export default DashboardPage
